.service {
	padding-top: 89px;
	padding-bottom: 127px;
	background-color: $second-accent;

	@media (max-width: 1220px) {
		padding-top: 50px;
		padding-bottom: 32px;
	}

  @media (max-width: 1220px) {
		padding-bottom: 34px;
	}
}

.service__wrapper {
	max-width: 1170px;
	margin: 0 auto;
	padding-left: 25px;
	padding-right: 25px;

	@media (max-width: $mobile-width) {
		padding-left: 15px;
		padding-right: 15px;
	}
}

.service__block-title {
	margin: 0;
	margin-bottom: 51px;
	font-family: 'Roboto Condensed';
	font-weight: 400;
	font-size: 36px;
	line-height: 51px;
	color: $white;

	@media (max-width: $tablet-width) {
		width: 630px;
		margin-bottom: 49px;
		font-size: 32px;
		line-height: 45px;
	}

	@media (max-width: $mobile-width) {
		width: auto;
    font-size: 28px;
		line-height: 39px;
    padding-left: 10px;
    margin-bottom: 42px;
	}
}

.service__list {
	display: flex;
	justify-content: space-between;
	margin: 0;
	padding: 0;
	list-style: none;

	@media (max-width: 1220px) {
		flex-direction: column;
	}
}

.service__item {
	box-sizing: border-box;
	width: 370px;
	height: 620px;
	background-repeat: no-repeat;
	background-size: contain;

	@media (max-width: 1220px) {
		width: 715px;
		height: 256px;
		margin: 0 auto;
		margin-bottom: 46px;
		margin-left: 1px;
	}

	@media (max-width: $mobile-width) {
		width: 290px;
		height: 256px;
		margin-bottom: 30px;
    margin-left: auto;
    margin-right: auto;
	}

	&--production {
		background-image: url(../img/background-production-desktop.jpg);

		@media (max-width: 1220px) {
			background-image: url(../img/background-production-tablet.jpg);
		}

		@media (max-width: $mobile-width) {
			background-image: url(../img/background-production-mobile.jpg);
		}
	}

	&--installation {
		background-image: url(../img/background-installation-desktop.jpg);

		@media (max-width: 1220px) {
			background-image: url(../img/background-installation-tablet.jpg);
			background-size: 100% 101%;
		}

		@media (max-width: $mobile-width) {
			background-image: url(../img/background-production-mobile.jpg);
		}
	}

	&--light {
		background-image: url(../img/background-light-desktop.jpg);

		@media (max-width: 1220px) {
			background-image: url(../img/background-light-tablet.jpg);
			background-size: 100% 101%;
		}

		@media (max-width: $mobile-width) {
			background-image: url(../img/background-production-mobile.jpg);
		}
	}
}

.service__link {
	position: relative;
	display: block;
	box-sizing: border-box;
	width: 100%;
	height: 100%;
	padding-top: 30px;
	padding-left: 44px;
	padding-right: 44px;

	&:focus,
	&:hover {
		background-color: rgba($black, 0.4);
	}

	&::before {
		position: absolute;
		right: 0;
		bottom: 0;
		content: '';
		width: 63px;
		height: 63px;
		background-color: $first-accent;
		background-image: url(../img/icon-arrow-menu.svg);
		background-repeat: no-repeat;
		background-position: center;
	}

	@media (max-width: 1220px) {
		padding-top: 21px;
	}

  @media (max-width: $mobile-width) {
		padding-top: 20px;
    padding-left: 34px;
	}
}

.service__title {
	width: 165px;
  margin: 0;
	font-family: 'Roboto Condensed';
	font-weight: 400;
	font-size: 20px;
	line-height: 30px;
	color: $white;
}
